import React, { useState, useEffect } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
// Import Swiper styles
import 'swiper/css';
import 'swiper/css/effect-fade';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import 'swiper/css/scrollbar';
// import required modules
import { EffectFade, Autoplay, Navigation } from 'swiper/modules';
import KeyboardDoubleArrowRightIcon from '@mui/icons-material/KeyboardDoubleArrowRight';
import KeyboardDoubleArrowLeftIcon from '@mui/icons-material/KeyboardDoubleArrowLeft';
import BtnLink from '../BtnLink';
import './HomeBanner.css';

const HomeBanner = () => {

    const [banners, setBanner] = useState([]);

    useEffect(() => {
        getBanners();
    }, []);

    const getBanners = async () => {
        let result = await fetch(`${process.env.REACT_APP_BASE_URL}banners`);
        result = await result.json();
        if (result.status) {
            setBanner(result.banners);
        }
    }

    return (
        <>
             <div className='ban-slide'>
            <Swiper
                // effect={'fade'}
                loop={true}
                speed={1000}
                autoplay={{
                    delay: 6500,
                    disableOnInteraction: false,
                }}
                // navigation={true}
                navigation={{
                    prevEl: '.prev-slide',
                    nextEl: '.next-slide',
                  }}
                modules={[EffectFade, Autoplay, Navigation]}
                className="home-slider">
                <SwiperSlide>
                        <div className='container'>
                            <div className='row'>
                                <div className='col-lg-6 '>
                                  <div className='slide-content'>
                                    <span className='slide-title-sm'>Small Title !</span>
                                    <p className='slide-title'>Banner Title main with small description</p>
                                    <p className='slide-desc'>Get 21% off this season. </p>
                                    <BtnLink Href="#" btnName="Explore Now"></BtnLink>
                                  </div>
                                </div>
                                <div className='col-lg-6'>
                                    <div className='slide-img-box'>
                                        <img className='slide-img-bg' src="images/bg/slide_shape.png" alt="" />
                                       <img className='slide-img' src="images/bags/21d.png" alt="" />
                                    </div>
                                </div>
                            </div>
                        </div>
                </SwiperSlide>
                <SwiperSlide>
                        <div className='container'>
                            <div className='row'>
                                <div className='col-lg-6 '>
                                  <div className='slide-content'>
                                    <span className='slide-title-sm'>Small Title !</span>
                                    <p className='slide-title'>Banner Title main with small description</p>
                                    <p className='slide-desc'>Get 21% off this season. </p>
                                    <BtnLink Href="#" btnName="Explore Now"></BtnLink>
                                  </div>
                                </div>
                                <div className='col-lg-6'>
                                    <div className='slide-img-box'>
                                        <img className='slide-img-bg' src="images/bg/slide_shape.png" alt="" />
                                       <img className='slide-img' src="images/bags/21d.png" alt="" />
                                    </div>
                                </div>
                            </div>
                        </div>
                </SwiperSlide>

                <div className="prev-slide slide-btn"><span><KeyboardDoubleArrowLeftIcon/></span></div>
                <div className="next-slide slide-btn"><span><KeyboardDoubleArrowRightIcon /></span></div>
            </Swiper>
        </div>
        </>
    )
}

export default HomeBanner
